export default {
	data() {
		return {
			// 成人人数
			adultNum: null,
			// 儿童人数
			childrenNum: null
		};
	},
	methods: {
		// 去确认订单页面
		goConfirmationOrder() {
			uni.navigateTo({
				url: '/pages/confirmationOrder/confirmationOrder'
			})
		}
	},
	onLoad(options) {
		if (options.adultNum !== undefined && options.childrenNum !== undefined) {
			this.adultNum = parseInt(options.adultNum);
			this.childrenNum = parseInt(options.childrenNum);
		} else {
			this.adultNum = 1
			this.childrenNum = 1
		}
	}
};
